const SET_BATTLEDEX = function (state, payload) {
    state.battledex = payload
}

const SET_SELECTED_POKEMON = function (state, pokemon) {
    state.selectedPokemon = pokemon
}

const SET_LOADING_STATE = function (state, isLoading) {
    state.isLoading = isLoading
}



export default {
    SET_BATTLEDEX,
    SET_LOADING_STATE,
    SET_SELECTED_POKEMON
}