<template>
  <div id='app' class="theme">
    <NavBar></NavBar>
    <!-- page__container at tier -->
    <transition :name="transitionName">
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import NavBar from "@/components/NavBar";
const DEFAULT_TRANSITION = 'fade';

export default {
  components: {
    NavBar
  },
  data(){
    return {
      transitionName: DEFAULT_TRANSITION,
    }
  },
  created(){
    this.$router.beforeEach((to, from, next)=> {
      let transitionName = to.meta.transitionName || from.meta.transitionName;
      if (transitionName === 'slide') {
        const toDepth = to.path.split('/').length;
        const fromDepth = from.path.split('/').length;
        transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left';
      }
      this.transitionName = transitionName || DEFAULT_TRANSITION;
      next();
    })
  }
};
</script>

<style lang="scss">

#app {
  display: table;
}

$stats: (
  hp: #FF0000, atk: #F08030, def: #F8D030, spa:#6890F0, spd: #78C850, spe:#F85888
);

$lighter: (
  hp: #FF5959, atk: #F5AC78, def: #FAE078, spa:#9DB7F5, spd: #A7DB8D, spe:#FA92B2
);

@each $stat, $hexColor in $stats {
  .#{$stat} {
    background-color: $hexColor;
  }
  .#{$stat}-font {
    color: $hexColor;
  }
}

@each $stat, $hexColor in $lighter {
  .#{$stat}-lighter {
    background-color: $hexColor;
  }
  .#{$stat}-font-lighter {
    color: $hexColor;
  }
}

.hidden {
  overflow: hidden;
}
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.3s;
  transition-property: opacity;
  transition-timing-function: ease;
}

.fade-enter,
.fade-leave-active {
  opacity: 0
}

.slide-left-enter-active,
.slide-left-leave-active,
.slide-right-enter-active,
.slide-right-leave-active {
  transition-duration: 0.5s;
  transition-property: height, opacity, transform;
  transition-timing-function: cubic-bezier(0.55, 0, 0.1, 1);
  overflow: hidden;
}

.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  transform: translate(2em, 0);
}

.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translate(-2em, 0);
}

</style>

